.multiSelectContainer {
  margin-bottom: var(--input-container-margin-bottom);
  width: var(--input-desktop-width);
}
.multiSelectContainer .chevronPositioning {
  align-items: center;
  display: flex;
  position: relative;
  width: fit-content;
}
.multiSelectContainer .chevronPositioning svg {
  transform: rotate(90deg);
  pointer-events: none;
  position: absolute;
  right: 1rem;
  top: 42%;
}
.multiSelectContainer .chevronPositioning .activeChevron svg {
  transform: rotate(-90deg);
}
@media (max-width: 500px) {
  .multiSelectContainer .chevronPositioning {
    width: 100%;
  }
}
.multiSelectContainer .multiSelectButton {
  appearance: none;
  background: none;
  border-radius: var(--input-border-radius);
  border: var(--input-border);
  font-size: var(--input-font-size);
  margin: var(--input-margin);
}
.multiSelectContainer .multiSelectButton input[aria-label="Search options"] {
  font-size: var(--input-font-size);
  height: var(--input-height);
  padding-right: var(--spacing-l);
  padding: var(--input-padding);
  text-align: left;
  width: var(--input-desktop-width);
}
.multiSelectContainer .multiSelectButton input[aria-label="Search options"]:focus-visible {
  border: var(--input-border-focus);
}
.multiSelectContainer .multiSelectButton input[aria-label="Search options"]:hover {
  border: var(--input-border-hover);
}
@media (max-width: 500px) {
  .multiSelectContainer .multiSelectButton input[aria-label="Search options"] {
    width: 100%;
  }
}
@media (max-width: 500px) {
  .multiSelectContainer .multiSelectButton {
    width: 100%;
  }
}
.multiSelectContainer .multiSelectDropdown {
  background: #fff;
  border: 1px solid #ccc;
  left: 0;
  position: absolute;
  top: 100%;
  width: 100%;
  z-index: 1;
}
.multiSelectContainer .multiSelectSearch {
  border: 1px solid #ccc;
  padding: var(--spacing-xs);
  width: calc(100% - 16px);
}
.multiSelectContainer .multiSelectList {
  list-style: none;
  padding: 0;
  margin: 0;
  max-height: 200px;
  overflow-y: auto;
}
.multiSelectContainer .multiSelectItem label {
  display: flex;
  align-items: center;
}
.multiSelectContainer .multiSelectItem label input {
  margin: var(--spacing-xs);
  margin: 12px;
}
.multiSelectContainer .multiSelectItem:hover {
  background-color: var(--theme-a-1);
  border-bottom: 1px solid var(--theme-a-3);
}
.multiSelectContainer .hoverActive {
  background-color: var(--theme-a-1);
  border-bottom: 1px solid var(--theme-a-3);
}
.multiSelectContainer .selectAll {
  border-bottom: 1px solid var(--utility-neutral-20);
  font-weight: 700;
}
.multiSelectContainer .input[aria-invalid=true] {
  border-color: var(--input-invalid-border-color);
  border-left-width: var(--input-invalid-border-left-width);
}
.multiSelectContainer .chipContainer {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  gap: var(--spacing-xs);
  margin: var(--spacing-xs) 0 var(--spacing-xxs) 0;
  width: 100%;
}
.multiSelectContainer .chipContainer .cancelLink button {
  font-weight: 400;
  margin: var(--spacing-xxs) 12px;
  text-decoration: underline;
}
@media (max-width: 500px) {
  .multiSelectContainer {
    width: 100%;
  }
}